<template>
  <li>
    <div class="item-img">
      <img :src="src">
      <div class="left fl">
        <div class="title">{{name}}</div>
        <div class="desc">{{cinemaCount}}家影院上映 {{watchCount}}人购票</div>
      </div>
      <div class="right fl">
        <div class="score">{{grade}}</div>
      </div>
    </div>
  </li>
</template>

<script>
    export default {
      name:"MovieItem",
      props:["src","name","cinemaCount","watchCount","grade"],
      data() {
        return {

        }
      }
    };
</script>

<style scoped lang="scss">
  .item-img{
    width: 2.86rem;
    height:2.1rem;
    margin:auto;
    margin-top:0.16rem;
    color: #333;
    border: 1px solid #ccc;
    background-image: url("/static/images/loading.png");
    background-size: 100% 100%;
    background-position: center center;
    img{
      width:2.86rem;
      height:1.6rem;
      float:left;
    }
    div{
      box-sizing: border-box;
    }
    .left{
      width:2.35rem;
      height: 0.5rem;
      padding-right: 0;
      padding-top: 0.13rem;
      padding-left: 0.1rem;
      .title{
        font-size: 0.12rem;
        line-height: 0.15rem;
        -webkit-user-select: text;
        user-select: text;
      }
      .desc{
        font-size: 0.08rem;
        color: #9a9a9a;
        line-height: 0.15rem;
      }
    }
    .right{
      color: #f78360;
      line-height: 0.5rem;
      float: right;
      margin-right: 0.15rem;
      font-size: 0.18rem;
    }
  }
</style>



